.layoutContent{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 0px 3.5rem;
    background-color: var(--color-background-content);
}

@media (max-width: 75em){
    .layoutContent{
      -ms-flex-align: center;
          align-items: center;
      height: 100vh;
    }
}

@media (max-width: 37.5em){
    .layoutContent{
      padding: 0 1.5rem;
    }
}

.contentHeadingText{
    font-size: 2.4rem;
    font-weight: 400;
    color: var(--color-heading-text);
    margin: 3rem 0rem;
}

@media (max-width: 75em){
    .contentHeadingText{
        text-align: center;
        width: 85%;
    }
}


.contentMainArticles{
    margin-bottom: 3rem;
    background-color: var(--color-white);
    -webkit-box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
            box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
    padding: 1.5rem;
    border-radius: .8rem;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.contentImage{
    width: 12.5rem;
    height: 12.5rem;
    border-radius: 50%;
    margin-right: 1rem;
    border: 2px solid var(--color-border-sidebar);
    -webkit-box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
            box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
}

.contentText{
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 1rem;
    display: -ms-flexbox;
    display: flex;
}

.TextContainer{
    width: 75%;
}

@media (max-width: 56.2em){
    .TextContainer{
      width: 100%;
      padding: 0rem;
    }
}

.IconContainer{
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 1rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
}

@media (max-width: 56.2em){
    .IconContainer{
        display: none;
    }
}

.ImageContainer{
    background-color: var(--color-border-sidebar);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border-radius: 50%;
    border: 1px solid var(--color-border-sidebar);
    -webkit-box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
            box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
}

.ImageContainer:hover, 
.ImageContainer:visited
{
    background-color: var(--color-item-selected-sidebar);
    cursor: pointer;
}

.Image{
    width: 1.75rem;
    height: 1.75rem;
    margin: .5rem;
}

.title{
    display: block;
    font-size: 1.75rem;
    font-weight: 700;
    color: var(--color-item-selected-sidebar);
}

.byLine{
    display: block;
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--color-item-sidebar);
}

.date{
    display: block;
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--color-item-sidebar);
}

.abstract{
    width: 75%;
    margin-top: 1rem;
    display: block;
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--color-heading-text);
}

@media (max-width: 56.25em){
    .title{
        font-size: 2rem;
    }

    .byLine{
        font-size: 1.75rem;
    }

    .date{
        font-size:1.65rem;
    }

    .abstract{
      width: 100%;
      font-size: 1.65rem;
    }
}
  
.Bold{
    color: var(--color-item-sidebar);
    font-weight: 700;
}

.contentMainHeadline{
    width: 75%;
    margin: 3.5rem 0rem;
    text-align: center;
}

.contentMainHeadlineText{
    font-size: 4rem;
    color: #7B8C92;
    font-weight: 700;
}

.contentMainPlaceholder{
    margin-bottom: 16px;

}

.contentPlaceholderImg{
    width: 42.5rem;
    height: 36rem;
}

.ButtonContainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 1rem;
    margin-bottom: 2rem;
}

.searchButton{
    background-color: var(--color-item-selected-sidebar);
    border:  .2rem solid var(--color-item-selected-sidebar);
    border-radius: 2.6rem;
    padding: 1.5rem 4.5rem;
    color: var(--color-white);
    font-size: 1.8rem;
    font-family: inherit;
    font-weight: 700;
    cursor: pointer;
    -webkit-box-shadow: 0 0 5px 0.5px var(--color-item-selected-sidebar);
            box-shadow: 0 0 5px 0.5px var(--color-item-selected-sidebar);
}

.searchButton:hover{
    background-color: var(--color-white);
    color: var(--color-item-selected-sidebar);
    border: .2rem solid var(--color-border-sidebar);
    -webkit-box-shadow: 0 0 .5rem 0.05rem var(--color-border-sidebar);
            box-shadow: 0 0 .5rem 0.05rem var(--color-border-sidebar);
}

.searchButton:focus{
    outline: none;
}

.mobileLink{
    margin-top: 1rem;
    margin-bottom: auto;
    padding: 0.5rem 1rem;
    font-family: inherit;
    text-decoration: none;
    font-size: 1.65rem;
    font-weight: 700;
    border-radius: 1.75rem;
    color: var(--color-white);
    -webkit-box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
            box-shadow: .2rem .8rem 1.9rem var(--color-shadaow);
    background-color: var(--color-item-selected-sidebar);
}

@media (min-width: 56.2em){
    .mobileLink{
        display: none;
    }
}

.drawerLogo{
    width: 3.5rem;
    height: 3.5rem; 
    margin-right: 1rem;
    overflow: hidden;
    -ms-flex:start 1;
        flex:start 1;
    cursor: pointer;
}

@media (min-width: 75em){
    .drawerLogo{
        display: none;
    }
}


