.layoutSidebar{
    width: 275px!important;
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: var(--color-background-sidebar);
    border: 1px solid var(--color-border-sidebar);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    -o-transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;

}

@media (max-width: 75em){
    .layoutSidebar {
        position: fixed;
        left: 0;
        top: 0;
        z-index: 200;
    }
}

.Open {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
}

.Close {
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
}

.sidebarLogo{
    padding: 32px 0px 25px 40px;
}

.sidebarContent{
    -ms-flex: 1 1;
        flex: 1 1;
}

.sidebarLogoImg{
    width: 62.5%;
}

.sidebarLink{
    display: block;
    padding: 10px 0px 10px 40px;
    text-decoration: none;
    font-weight: 500;
    font-size: 2rem;
    cursor: pointer;
    color: var(--color-item-sidebar);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

@media (max-width: 56.25em){
    .sidebarLink {
        font-size: 2.5rem;
        font-size: 400;
    }
}

.sidebarLinkActive{
    display: block;
    padding: 10px 0px 10px 40px;
    text-decoration: none;
    font-weight: 500;
    font-size: 2rem;
    cursor: pointer;
    color: var(--color-item-selected-sidebar);
    background-color: var(--color-white);
    -webkit-box-shadow: 0 0 5px 0.5px var(--color-border-sidebar);
            box-shadow: 0 0 5px 0.5px var(--color-border-sidebar);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}


@media (max-width: 56.25em){
    .sidebarLinkActive {
        font-size: 2.5rem;
        font-size: 400;
    }
}


.Icon{
    width: 17.5px;
    height: 17.5px;
    margin-right: 10px;
}

.footer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 20px 0px;
}

.footerText{
    font-size: 1.25rem;
    font-weight: 400;
    color: var(--color-toggle-button);
}


